import React from 'react';
import styled, { keyframes } from 'styled-components';

const flicker = keyframes`
  from {
    color: #0c0c0c;
    background-color: #ffffff;
  }
  to {
    color: #61dafb;
    background-color: #EE7752;
  }
`

const ScheduleRow = styled.div`
  display: flex;
  flex-direction: row;
  align-content: flex-start;
  z-index: 2;
`

const ScheduleItem = styled.div`
  min-height: 100px;
  background-color: #fff;
  padding: 10px;
  color: #0c0c0c;
  min-width: 150px;
  border-radius: 4px;
  margin: 10px;
  
  &:hover {
    animation: ${flicker} 0.1s infinite;
  }
`

const Schedule = () => (
    <ScheduleRow>
        <ScheduleItem>
            <h1>BILLY EILISH</h1>
        </ScheduleItem>

        <ScheduleItem>
            <h1>RADIO LIMA</h1>
        </ScheduleItem>

        <ScheduleItem>
            <h1>KARAOKE</h1>
        </ScheduleItem>

        <ScheduleItem>
            <h1>RADIO LIMA</h1>
        </ScheduleItem>

        <ScheduleItem>
            <h1>RADIO LIMA</h1>
        </ScheduleItem>

        <ScheduleItem>
            <h1>RADIO LIMA</h1>
        </ScheduleItem>

        <ScheduleItem>
            <h1>RADIO LIMA</h1>
        </ScheduleItem>

        <ScheduleItem>
            <h1>RADIO LIMA</h1>
        </ScheduleItem>

    </ScheduleRow>
)

export { Schedule }
